<template>
  <div class="recognition">
    <meta name = "format-detection" content = "telephone=no">
    <div style="height:1.22667rem">
      <navigation-bar :pageName="pageName" @onLeftClick="goBack">
        <slot slot="nav-right" name="right" >
          <div class="nav-right" @click="popArea=!popArea">
            <div >{{faceStateName}}</div>
          </div>
        </slot>
      </navigation-bar>
    </div>
    <div class="pop-area" v-show="popArea">
      <div class="square"></div>
      <div class="pop-text">
        <p class="text-border" @click="selectFaceStateList(1)">已录入人脸</p>
        <p @click="selectFaceStateList(0)">未录入人脸</p>
      </div>
    </div>
    <div class="content">
      <div style="height:2.1rem">
        <div class="top">
          <!-- <div class="select-area" v-show="selectBar">
					<input type="text" placeholder="请输入楼号" />
					<span></span>
					<input type="text" placeholder="请输入单元号" />
					<span>单元</span>
					<input type="text" placeholder="请输入房间号" />
					<span class="search-btn">查找</span>
          </div>-->
          <div class="select-area">
            <div class="location">{{ communityName }}</div>
            <div class="build">
              <van-field
                readonly
                clickable
                label="楼栋"
                label-width="0.8rem"
                right-icon="arrow-down"
                :value="roomObj.buildVal"
                @click="getBuildData"
              />
              <van-popup v-model="buildPicker" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="buildData"
                  value-key="buildName"
                  @cancel="buildPicker = false"
                  @confirm="onConfirmBuild"
                />
              </van-popup>
            </div>
            <div class="unit">
              <van-field
                readonly
                clickable
                label="单元"
                label-width="0.8rem"
                :value="roomObj.unitVal"
                right-icon="arrow-down"
                @click="getUnitData"
              />
              <van-popup v-model="unitPicker" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="unitData"
                  @cancel="unitPicker = false"
                  @confirm="onConfirmUnit"
                />
              </van-popup>
            </div>
            <div class="room">
              <van-field
                readonly
                clickable
                label="房间"
                label-width="0.8rem"
                :value="roomObj.roomVal"
                right-icon="arrow-down"
                @click="getRoomData"
              />
              <van-popup v-model="roomPicker" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="roomData"
                  value-key="roomName"
                  @cancel="roomPicker = false"
                  @confirm="onConfirmRoom"
                />
              </van-popup>
            </div>
          </div>
          <div class="search-area">
            <van-search
              v-model="searchVal"
              placeholder="请输入姓名或手机号"
              @search="onSearch"
            />
            <span class="search-btn" @click="onSearch(searchVal)">搜索</span>
            <!--<div class="arrow" @click="selectBar=!selectBar" :class="selectBar?'up':''"></div>-->
          </div>
        </div>
      </div>
      <!-- <div style="overflow: scroll"> -->
      <van-pull-refresh v-model="isPulling" @refresh="onRefresh">
        <div class="face-list" v-show="faceInfoList.length>0">
          <van-list
            v-model="loading"
            :finished="finished"
            @load="onLoad"
            finished-text="—— 我是有底线的 ——"
            :offset="10"
          >
            <div class="item" v-for="(item,index) in faceInfoList" :key="index">
              <div class="left">
                <div class="name">{{item.name}}</div>
                <div class="number">{{item.phone}}</div>
              </div>
              <div class="right" @click="goDetail(item.id)">
                <button class="btn" v-if="item.picIds.length<=0">录入人脸</button>
                <span v-else>已录入人脸</span>
              </div>
            </div>
          </van-list>
        </div>
        <div class="no-list" v-show="faceInfoList.length==0">—— 暂无数据 ——</div>
      </van-pull-refresh>
      </div>
