        <div class="item" v-for="(item,index) in faceList" :key="index">
          <div class="left">
            <div class="name">{{item.name}}</div>
            <div class="number">{{item.number}}</div>
          </div>
          <div class="right" @click="goDetail(index)">
            <button class="btn" v-if="!item.hasImg">录入人脸</button>
            <span v-else>已录入人脸</span>
          </div>
        </div>
      </div>-->
    </div>
  <!-- </div> -->
</template>

<script>
import { Search, PullRefresh } from "vant";
import { store } from "@/utils/store";
import {
  selectRegion,
  selectBuild,
  selectUnit,
  selectRoom,
  getFaceInfoList
} from "xt/services/recognition.js";
import NavigationBar from "../../../title/NavigationBar";
export default {
  name: "recognition",
  components: {
    NavigationBar,
    [Search.name]: Search,
    [PullRefresh.name]: PullRefresh
  },
  data() {
    return {
      pageName:"业主人脸录入",
      searchVal: "",
      pageNum: 1,
      total: 0,
      roomInfo: "",
      faceState: "",
      popArea: false,
      // selectBar: false,

      regionId: "20180820013", // 小区id
      communityName: "未知", // 小区名字

      buildData: [],
      buildPicker: false,
      unitData: [],
      unitPicker: false,
      roomData: [],
      roomPicker: false,
      roomObj: {
        buildVal: "",
        buildId: "",
        unitVal: "",
        roomVal: ""
      },

      loading: false,
      isPulling: false, // 下拉刷新
      finished: false, // 列表数据是否全部加载完
      faceInfoList: [],

      faceName: "",
      faceNum: "",
      faceImg: ""
    };
  },
  mounted() {
    console.log("--人脸列表--");
    this.regionId = localStorage.getItem("userInfo").AREA_CODE;
    this.communityName = localStorage.getItem("userInfo").AREA_NAME || "未知";
    this.getFaceList();
    // this.getCommunityName();
  },
  computed:{
    faceStateName(){
      if(this.faceState.length==0){
        return "筛选";
      }else if(this.faceState==1){
        return "已录";
      }else {
        return "未录";
      }
    }
  },
  methods: {
    goBack() {
      this.$router.push({
        path: "/"
      });
    },
    // 已、未录入人脸
    selectFaceStateList(val) {
      this.faceState = val;
      this.pageNum = 1;
      this.faceInfoList = [];
      this.getFaceList();
      this.popArea = false;
    },
    // 获取小区名字
    getCommunityName() {
      let data = {
        regionId: this.regionId
      };
      this.loading = true;
      let that = this;
      selectRegion(data)
        .then(res => {
          that.loading = false;
          that.communityName = res.data || "未知";
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    // 楼栋
    getBuildData() {
      this.buildPicker = true;
      let data = {
        regionId: this.regionId
      };
      this.loading = true;
      let that = this;
      selectBuild(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.buildData = res.data;
          } else {
            that.$toast(res.message);
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    onConfirmBuild(item) {
      this.roomObj.buildVal = item.buildName;
      this.roomObj.buildId = item.id;
      this.pageNum = 1;
      this.faceInfoList = [];
      this.getFaceList();
      this.buildPicker = false;
    },
    // 单元
    getUnitData() {
      if (!this.roomObj.buildId) {
        this.$toast("请选择楼栋");
        return;
      }
      this.unitPicker = true;
      let data = {
        areaBuildsId: this.roomObj.buildId
      };
      this.loading = true;
      let that = this;
      selectUnit(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.unitData = res.data;
          } else {
            that.$toast(res.message);
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    onConfirmUnit(item) {
      this.roomObj.unitVal = item;
      this.pageNum = 1;
      this.faceInfoList = [];
      this.getFaceList();
      this.unitPicker = false;
    },
    // 房间
    getRoomData() {
      if (!this.roomObj.buildId) {
        this.$toast("请选择楼栋");
        return;
      }
      if (!this.roomObj.unitVal) {
        this.$toast("请选择单元");
        return;
      }
      this.roomPicker = true;
      let data = {
        areaBuildsId: this.roomObj.buildId,
        unitCode: this.roomObj.unitVal
      };
      this.loading = true;
      let that = this;
      selectRoom(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.roomData = res.data;
          } else {
            that.$toast(res.message);
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    onConfirmRoom(item) {
      this.roomObj.roomVal = item.roomName;
      this.roomInfo = item.roomName;
      this.pageNum = 1;
      this.faceInfoList = [];
      this.getFaceList();
      this.roomPicker = false;
    },
    goDetail(id) {
      this.$router.push({
        path: "/recognitionDetail",
        query: { id }
      });
    },
    // 获取人脸列表
    getFaceList() {
      this.loading = true;
      let that = this;
      let data = {
        pageNum: this.pageNum,
        pageSize: 20,
        groupId: 1,
        name: this.searchVal,
        buildId: this.roomObj.buildId,
        unitId: this.roomObj.unitVal,
        roomId: this.roomInfo,
        faceState: this.faceState
      };
      getFaceInfoList(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.faceInfoList = that.faceInfoList.concat(res.data.list || []);
            that.total = res.data.total;
          } else {
            that.$toast(res.message);
          }
          that.finished = false;
          if (that.faceInfoList.length >= that.total) {
            that.finished = true; //此时不再执行onLoad事件
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    //下拉刷新
    onRefresh() {
      // if (this.loading) {
      //   return;
      // }
      setTimeout(() => {
        this.isPulling = false;
        this.finished = false;

        this.faceState = ""; // 已录入、未录入
        this.roomObj.buildVal = ""; // 楼栋
        this.roomObj.buildId = "";
        this.roomObj.unitVal = ""; // 单元
        this.roomObj.roomVal = ""; // 房间
        this.roomInfo = "";
        // this.searchVal = ""; // 搜索框

        this.pageNum = 1;
        this.faceInfoList = [];
        this.getFaceList();
      }, 1000);
    },
    //上拉加载
    onLoad() {
      // if (this.loading) {
      //   return;
      // }
      this.finished = false;
      setTimeout(() => {
        if (this.faceInfoList.length >= this.total) {
          this.finished = true; //此时不再执行onLoad事件
          return;
        }
        this.isPulling = false;
        this.finished = false;
        this.pageNum++;
        console.log("上拉加载");
        this.getFaceList();
      }, 500);
    },
    onSearch(val) {
      this.searchVal = val;
      this.pageNum = 1;
      this.faceInfoList = [];
      this.getFaceList();
    }
  }
};
</script>

