// 获取照片
const getPic = (id) => {
  return axios({
    url: storage.getStorage('xtBaseUrl') + "/subsystem/api/support/ftps/download1?id=" + id,
    method: "get",
    responseType: "arraybuffer",
    ignoreCodeValidation: true
  });
};


export {
  selectRegion,
  selectBuild,
  selectUnit,
  selectRoom,
  getFaceInfoList,
  getFaceInfoDetail,
  getPic,
  baseUrl
}